import React, { Component } from 'react';
import Slider from 'react-slick';
import { IconButton, Grid } from '@material-ui/core';
import {
  KeyboardArrowRight as ArrowRightIcon,
  KeyboardArrowLeft as ArrowLeftIcon,
} from '@material-ui/icons';
import {
  IconCheckCircle,
  IconLink,
  IconChartWaterfall,
  IconShareNode,
  IconWindowRestore,
  IconLaptopMobile,
  IconLongArrowRight
} from '../../../../images/CustomIcon';
import StartPageLink from '../../../start-page/components/StartPageLink';
import KSIllustrationImg from '../../../../../assets/img/ks-illustration.svg';
import RAIllustrationImg from '../../../../../assets/img/ra-illustration.svg';
import TCIllustrationImg from '../../../../../assets/img/tc-illustration.svg';
import { Link } from '../../../../utils/Constants';
import KSActionService from "../../../../services/KSActionService";

const slides = [
  {
    background: '#ecf4f2',
    title: 'Enrich Katalon Studio experience',
    item1: {
      icon: <IconCheckCircle />,
      text: 'Provide more insights on test execution results',
    },
    item2: {
      icon: <IconLink />,
      text: 'Integrate seamlessly with Platform analytics for a comprehensive quality picture',
    },
    linkDoc: Link.enrichKSDoc,
    image: KSIllustrationImg,
  },
  {
    background: '#f1f2ff',
    title: 'Quality management with Analytics',
    item1: {
      icon: <IconChartWaterfall />,
      text: 'Centralized reporting and analytics for better visibility into state of quality',
    },
    item2: {
      icon: <IconShareNode />,
      text: 'Enhanced collaboration with team members by sharing test results and reports',
    },
    linkDoc: Link.qualityMADoc,
    image: RAIllustrationImg,
  },
  {
    background: '#e4eeff',
    title: 'Cross-browser testing with TestCloud',
    item1: {
      icon: <IconWindowRestore />,
      text: 'Optimize productivity with browsers, device or any OS combinations configurations',
    },
    item2: {
      icon: <IconLaptopMobile />,
      text: 'Streamline CI/CD pipeline with TestCloud Integration',
    },
    linkDoc: Link.testCloudDoc,
    image: TCIllustrationImg,
  },
];

const Slide = ({ background, title, item1, item2, image, linkDoc, isInKsProject }) => {

  const handleOpenIntegration =  () => {
    KSActionService.openIntegrateWithPlatform();
  }

  return (
      <div style={{ background, height: '265px', position: 'relative', paddingTop: '28px'}}>
        <div style={{ marginLeft: 30 }}>
          <h2 style={{ margin: 0 }}>{title}</h2>
          <Grid container spacing={2}>
            <Grid item xs={6}>
              <p style={{ display: "flex" }}>
                {item1.icon}
                <span style={{ marginLeft: '7px', fontSize: '12px', fontWeight: 400 }}>
                {item1.text}
              </span>
              </p>
              <p style={{ display: "flex" }}>
                {item2.icon}
                <span style={{ marginLeft: '7px', fontSize: '12px', fontWeight: 400 }}>
                {item2.text}
              </span>
              </p>
              <StartPageLink 
                onClick={isInKsProject ? handleOpenIntegration : null}
                href={!isInKsProject && linkDoc}
                area="testops"
                style={{
                  textTransform: "none",
                  backgroundColor: "#22283c",
                  color: "#ffffff",
                  fontSize: "12px",
                  boxShadow: "none",
                  fontWeight: 400
                }}
                asButton
              >
                Integrate with Platform
                <IconLongArrowRight style={{ marginLeft: "5px" }} />
              </StartPageLink>
            </Grid>
            <Grid item xs={6}>
          <span style={{ position: 'absolute', bottom: 0, right: 0 }}>
            <img src={image} alt="illustration" />
          </span>
            </Grid>
          </Grid>
        </div>
      </div>
  );
}

export default class StartPageBannerSlider extends Component {

  constructor(props) {
    super(props);
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
  }

  next() {
    this.slider.slickNext();
  }

  previous() {
    this.slider.slickPrev();
  }

  render() {
    const { isInKsProject } = this.props;
    const settings = {
      dots: true,
      infinite: true,
      speed: 1000,
      autoplay: false,
      autoplaySpeed: 6000,
      slidesToShow: 1,
      slidesToScroll: 1,
      dotsClass: 'slick-dots1',
      pauseOnHover: true,
      swipeToSlide: true,
      arrows: false,
    };

    return (
      <div>
        <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between", marginBottom: "12px" }}>
          <div style={{ fontSize: "12px", color: "#727993", fontWeight: 600 }}>PLATFORM</div>
          <span>
            <IconButton aria-label="arrow-back" size="small" onClick={this.previous}>
              <ArrowLeftIcon fontSize="inherit" />
            </IconButton>
            <IconButton aria-label="arrow-forward" size="small" onClick={this.next}>
              <ArrowRightIcon fontSize="inherit" />
            </IconButton>
          </span>
        </div>
        <Slider ref={c => (this.slider = c)} {...settings}>
          {slides.map((slide, index) => (
            <Slide key={index} {...slide} isInKsProject={isInKsProject} />
          ))}
        </Slider>
      </div>
    );
  }
}

