import { Button, Divider, Grid, makeStyles } from "@material-ui/core";
import { isArray } from 'lodash';
import { Skeleton } from "@material-ui/lab";
import { useState, useEffect } from "react";
import { IconBestPractices, IconChevronRight, IconEmptySet, IconExternalLinkGray, IconExternalLinkSolid } from "../../../../images/CustomIcon";
import KSService from "../../../../services/KSService";
import { Link } from "../../../../utils/Constants";
import ExecutionDecorator from "../../../../utils/decorator/ExecutionDecorator";

const styles = makeStyles({
  dashedBorder: {
    borderStyle: "dashed",
    color: "#dbdde5",
    margin: "0",
  },
  idSection: {
    display: "flex",
    fontSize: "14px",
    fontWeight: "500",
    color: "#233145",
    marginBottom: "8px",
  },
  detailSection: {
    marginTop: "2px",
    display: "flex",
    fontSize: "12px",
    color: "#808b9a",
    alignItems: "center",
  },
  testRunRow: {
    display: "flex",
    alignItems: "center",
    cursor: "pointer",
    padding: "7px 2px",
  },
  testRunHover: {
    '&:hover': {
      backgroundColor: "#F5F5F5",
      borderRadius: "4px",
    },
  },
  actionBtn: {
    alignItems: "center",
    fontSize: '12px',
    fontWeight: '400',
    textTransform: "none",
    color: "#808b9a",
  },
  iconExternalLink: {
    marginLeft: '5px',
  },
  scheduleTestBtn: {
    border: "solid 1px #22283c",
    textTransform: "none",
    marginTop: "3px",
    padding: "0px 9px",
    fontWeight: "600",
  },
  learnMoreBtn: {
    justifyContent: "flex-start",
    textTransform: "none",
    fontWeight: "normal",
    '&:hover': {
      backgroundColor: "transparent",
    },
  },
  lineDivider: {
    width: "0px",
    border: "1px dashed #dbdde5",
    backgroundColor: "transparent",
  },
  sectionDescription: {
    fontSize: "12px",
    marginLeft: "4px",
    marginBottom: "8px",
    color: "#4d5369",
  },
  iconEmptySet: {
    marginRight: "12px",
  },
  alignContent: {
    textAlign: "center",
  },
  emptyTest: {
    fontSize: "11px",
    color: "#727993",
    paddingBottom: "16px",
  },
  latestRunsTitle: {
    fontSize: "12px",
    fontWeight: 600,
    textAlign: "left",
    color: "#233145",
  },
  bestPracticesIcon: {
    marginRight: "-4px"
  },
});

export const LatestExecutions = ({testOpsServerUrl, projectLink, responseErrorChecking}) => {
  const classes = styles();

  const [latestExecutions, setLatestExecutions] = useState(null);

  const testRunLink = `${projectLink}/executions`;

  const testRunDetailLink = (executionId) => `${projectLink}/executions/${executionId}`;

  const projectExecutionLink = `${projectLink}/test-planning?isOpenScheduleTestRunDialog=true`;

  const openLink = (link) => {
    window.location.href = link;
  }

  const fetch3LatestExecutions = async (testOpsServerUrl) => {
    const data = await KSService.fetch3LatestExecutions(testOpsServerUrl);
    responseErrorChecking(data, () => {
      if (!isArray(data)) {
        setLatestExecutions([]);
      } else {
        setLatestExecutions(data);
      }
    });
  };

  useEffect(() => {
    if (testOpsServerUrl) {
      fetch3LatestExecutions(testOpsServerUrl);
    }
  }, []);

  const handleSelectRow = (row) => {
    openLink(testRunDetailLink(row.order))
  };

  const LatestExecutionsEmptyState = () => {
    return (
      <>
        <div className={classes.alignContent}>
          <div><IconEmptySet className={classes.iconEmptySet}/></div>
          <div className={classes.emptyTest}>
            There is no test run to display. To start monitoring test run history, you can:
          </div>
        </div>
        <Grid container justifyContent="center">
          <div>
            <div className={classes.sectionDescription}>Run a Test Suite in Katalon Studio.</div>
            <Button
              className={classes.learnMoreBtn}
              size="small"
              startIcon={<IconBestPractices className={classes.bestPracticesIcon} />}
              href={Link.executeTestSuiteDoc}
            >
              Learn more
            </Button>
          </div>
          <Divider orientation="vertical" className={classes.lineDivider} variant="middle" flexItem />
          <div>
            <div className={classes.sectionDescription}>Schedule a Test Run on Katalon Platform.</div>
            <Button
              className={classes.scheduleTestBtn}
              variant="outlined"
              size="small"
              endIcon={<IconExternalLinkSolid />}
              onClick={() => openLink(projectExecutionLink)}
            >
              Schedule Test
            </Button>
          </div>
        </Grid>
      </>
    );
  }

  const latestRunsTitle = () => {
    return (
      <strong className={classes.latestRunsTitle}>Latest Runs</strong>
    );
  }

  if (!latestExecutions) {
    return (
      <>
        {latestRunsTitle()}
        <Skeleton variant="rect" width="100%" height="100px" animation="wave" style={{ marginTop: '12px' }} />
      </>
    );
  }

  if (latestExecutions.length < 1) {
    return <LatestExecutionsEmptyState />
  }

  return (
    <div>
      {latestRunsTitle()}
      {latestExecutions.map((execution, index) => (
        <Grid className={classes.testRunHover} onClick={() => handleSelectRow(execution)}>
          <Grid className={classes.testRunRow}>
            <Grid xs={11}>
              <Grid item xs={12} className={classes.idSection}>
                {ExecutionDecorator.statusDecorator(execution)}
                {ExecutionDecorator.nameDecorator(execution)}
              </Grid>
              <Grid item xs={12} className={classes.detailSection}>
                {ExecutionDecorator.idDecorator(execution)}
                {ExecutionDecorator.startTimeDecorator(execution)}
                {ExecutionDecorator.durationDecorator(execution)}
                {ExecutionDecorator.oSDecorator(execution)}
                {ExecutionDecorator.browserDecorator(execution)}
                {ExecutionDecorator.profileDecorator(execution)}
                {ExecutionDecorator.asigneeDecorator(execution)}
              </Grid>
            </Grid>
            <Grid xs={1} style={{ display: "flex", justifyContent: "flex-end", marginRight: "10px" }}>
              <IconChevronRight />
            </Grid>
          </Grid>
          {index + 1 !== latestExecutions.length && <hr className={classes.dashedBorder} />} 
        </Grid>
      ))}
      <Button variant="text" className={classes.actionBtn} onClick={() => openLink(testRunLink)}>
        View All 
        <IconExternalLinkGray className={classes.iconExternalLink}/>
      </Button>
      <Button variant="text" className={classes.actionBtn} onClick={() => openLink(projectExecutionLink)}>
        Schedule Test
        <IconExternalLinkGray className={classes.iconExternalLink}/>
      </Button>
    </div>
  );
}
