import { 
  Button,
  Card,
  CardContent,
  Grid,
  makeStyles,
} from "@material-ui/core";
import { useState, useEffect } from "react";
import {
  IconExternalLink,
  IconProject,
  IconRefresh,
  IconSiteMap,
  IconTeam,
} from "../../../../images/CustomIcon";
import KSService from "../../../../services/KSService";
import { LatestExecutions } from "./LatestExecutions";
import { ExecutionStatistics } from "./ExecutionStatistics";
import PlatformSummaryPlaceholder from "./PlatformSummaryPlaceholder";
import StartPageBannerSlider from "./StartPageBannerSlider";
import { ErrorCardComponent } from "./ErrorCardComponent";

const styles = makeStyles({
  platformSummary: {
    fontFamily: "Inter, Roboto, Helvetica, Arial, sans-serif",
  },
  cardContent: {
    display: "flex",
    flexDirection: "column",
    gap: "6px",
    paddingBottom: "8px !important",
    borderRadius: "4px",
    border: "solid 1px #dbdde5",
  },
  title: {
    fontSize: "12px",
    fontWeight: 600,
    textAlign: "left",
    color: "#727993",
    paddingBottom: "13px",
  },
  detailsProjectSection: {
    display: "flex",
  },
  iconProject: {
    marginRight: '12px',
    minHeight: "32px",
    minWidth: "32px",
  },
  organizationTeamInfo: {
    marginTop: '5px',
    color: '#808b9a',
    fontSize: '12px',
  },
  iconSiteMap: {
    marginRight: '4px',
  },
  iconTeam: {
    marginLeft: '16px',
    marginRight: '4px',
  },
  actionSection: {
    display: "flex",
    justifyContent: "flex-end",
    height: "32px",
  },
  refreshBtn: {
    fontWeight: '400',
    textTransform: "none",
    color: "#808b9a",
  },
  iconRefresh: {
    marginRight: '4px',
  },
  iconBestPractices: {
    marginRight: '4px',
  },
  goToPlatformBtn: {
    fontWeight: '400',
    marginLeft: "5px",
    textTransform: "none",
    color: "#fff",
    backgroundColor: "#22283c",
    '&:hover': {
      color: "#fff",
      backgroundColor: "#22283c",
    },
  },
  iconExternalLink: {
    marginLeft: '5px',
  },
  executionStatisticsTitle: {
    fontSize: '10px',
    fontWeight: '400',
    color: '#808b9a',
  },
  projectName: {
    fontWeight: "600",
  }
});

export const PlatformSummary = ({elevation = 0}) => {
  const classes = styles();

  const [projectConfiguration, setProjectConfiguration] = useState(null);

  const [isRenderErrorComponent, setIsRenderErrorComponent] = useState(false);

  const fetchProjectConfiguration = async () => {
    const data = await KSService.fetchProjectConfiguration();
    responseErrorChecking(data, () => {
      setProjectConfiguration(data);
    });
  };

  const handleRefresh = async () => {
    setProjectConfiguration(null);
    fetchProjectConfiguration();
  };

  const responseErrorChecking = (data, callback) => {
    if (data?.hasError) {
      setIsRenderErrorComponent(true);
    } else {
      callback();
    }
  };

  const testOpsServerUrl = projectConfiguration?.testOpsServerUrl;

  const projectLink = `${testOpsServerUrl}/team/${projectConfiguration?.testOpsProject?.teamId}/project/${projectConfiguration?.testOpsProject?.id}`;

  const openProjectLink = async () => {
    window.location.href = projectLink;
  };

  useEffect(() => {
    fetchProjectConfiguration();
  }, []);

  const ProjectInformation = () => {
    return (
      <Grid container>
        <Grid xs={7} className={classes.detailsProjectSection}>
          <IconProject className={classes.iconProject}/>
          <Grid>
            <Grid>
              <div className={classes.projectName}>{projectConfiguration.testOpsProject?.name}</div>
            </Grid>
            <Grid container className={classes.organizationTeamInfo}>
              <IconSiteMap className={classes.iconSiteMap} />
              {projectConfiguration.testOpsProject?.organizationName} #{projectConfiguration.testOpsProject?.organizationId}
              <IconTeam className={classes.iconTeam} />
              {projectConfiguration.testOpsProject?.teamName}
            </Grid>
          </Grid>
        </Grid>
        <Grid xs={5} className={classes.actionSection}>
          <Button variant="text" className={classes.refreshBtn} onClick={handleRefresh}>
            <IconRefresh className={classes.iconRefresh}/>
            <span> Refresh </span>
          </Button>
          <Button evelation={0} variant="contained" color="success" className={classes.goToPlatformBtn} onClick={openProjectLink} disableElevation>
            Go to Platform
            <IconExternalLink className={classes.iconExternalLink}/>
          </Button>
        </Grid>
      </Grid>
    );
  };

  const ProjectExecutionSummary = () => {
    return (
      <>
        <div style={{ fontSize: "12px", color: "#727993", fontWeight: 600, marginBottom: "12px" }}>PLATFORM</div>
        <Card elevation={elevation} className={classes.card}>
          <CardContent className={classes.cardContent}>
            {<ProjectInformation />}
            <span className={classes.executionStatisticsTitle}>Last 7 days</span>
            {<ExecutionStatistics testOpsServerUrl={testOpsServerUrl} responseErrorChecking={responseErrorChecking} />}
            {<LatestExecutions testOpsServerUrl={testOpsServerUrl} projectLink={projectLink} responseErrorChecking={responseErrorChecking} />}
          </CardContent>
        </Card>
      </>
    );
  };

  const renderBody = () => {
    const { isInKsProject, isIntegrationEnabled } = projectConfiguration;
    if (isInKsProject) {
      return (
        <>
          {isIntegrationEnabled ? (
            <ProjectExecutionSummary />
          ) : (
            <StartPageBannerSlider isInKsProject />
          )}
        </>
      );
    }

    return <StartPageBannerSlider isInKsProject={isInKsProject} />;
  };

  return (
    <>
      {isRenderErrorComponent ? (
        <ErrorCardComponent />
      ) : (
        <div className={classes.platformSummary}>
          {projectConfiguration ? renderBody() : <PlatformSummaryPlaceholder />}
        </div>
      )}
    </>
  );
}
