import { Grid, makeStyles } from "@material-ui/core";
import { Skeleton } from "@material-ui/lab";
import React, { useEffect, useState } from "react";
import KSService from "../../../../services/KSService";
import { IconStatusFailed, IconStatusPassed } from "../../../../images/CustomIcon";
import ExecutionDecorator from "../../../../utils/decorator/ExecutionDecorator";
import { get } from 'lodash';

const styles = makeStyles({
  root: {
    gap: '16px',
    padding: 0,
    flexWrap: "nowrap",
  },
  content: {
    gap: '10px',
    padding: '12px 16px !important',
    borderRadius: '4px',
    backgroundColor: 'rgba(34, 40, 60, 0.04)'
  },
  title: {
    fontSize: '12px',
    paddingBottom: '6px',
    fontWeight: '400',
    color: '#808b9a'
  },
  value: {
    fontSize: '18px',
    fontWeight: '600',
    color: '#233145',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    "& span": {
      fontSize: '18px',
      fontWeight: 600,
      margin: '0 16px 0 8px'
    }
  },
  iconStatus: {
    width: '16px',
    height: '16px',
  }
});

export const ExecutionStatistics = ({testOpsServerUrl, responseErrorChecking}) => {
  const classes = styles();

  const [totalExecutionsStatistics, setTotalExecutionsStatistics] = useState(null);
  const [projectStatistics, setProjectStatistics] = useState(null);

  const fetchProjectStatisticsThisWeek = async (testOpsServerUrl) => {
    const data = await KSService.fetchProjectStatisticsThisWeek(testOpsServerUrl);
    responseErrorChecking(data, () => {
      const diff = calcDiff(data, 'totalDuration');
      data.diff = isNaN(parseFloat(diff)) ? 0 : diff;
      setProjectStatistics(data);
    });
  };

  const fetchTotalExecutionsStatisticsThisWeek = async (testOpsServerUrl) => {
    const data = await KSService.fetchTotalExecutionsStatisticsThisWeek(testOpsServerUrl);
    responseErrorChecking(data, () => {
      setTotalExecutionsStatistics(data);
    });
  };

  const calcDiff = (statistics, property) => {
    const current = get(statistics, property);
    const previous = get(statistics, `previousWeekStatistics.${property}`);
    return (((current - previous) * 100) / previous).toFixed(2);
  };

  useEffect(() => {
    if (testOpsServerUrl) {
      fetchTotalExecutionsStatisticsThisWeek(testOpsServerUrl);
      fetchProjectStatisticsThisWeek(testOpsServerUrl);
    }
  }, []);

  if (!totalExecutionsStatistics || !projectStatistics) {
    return <Skeleton variant="rect" width="100%" height="100px" animation="wave" />;
  }

  const getValue = (object, property) => {
    const val = get(object, property);
    if (val !== 0) return val;
    return "-";
  }

  return (
    <Grid container className={classes.root} columns={{ xs: 12, md: 12 }}>
      <Grid item md={4} xs={4} style={{paddingRight: "8px"}}>
        <div className={classes.content}>
          <div className={classes.title}>Executions</div>
          <div className={classes.value}>
            {getValue(totalExecutionsStatistics, 'totalExecutionThisWeek')}
            {ExecutionDecorator.trendDecorator(totalExecutionsStatistics.diff)}
          </div>
        </div>
      </Grid>
      <Grid item md={4} xs={4} style={{paddingRight: "8px", paddingLeft: "8px"}}>
        <div className={classes.content}>
          <div className={classes.title}>Execution Time</div>
          <div className={classes.value}>
            {ExecutionDecorator.executionTimeDecorator(projectStatistics)}
            {ExecutionDecorator.trendDecorator(projectStatistics.diff)}
          </div>
        </div>
      </Grid>
      <Grid item md={4} xs={4} style={{paddingLeft: "8px"}}>
        <div className={classes.content}>
          <div className={classes.title}>Test Results</div>
          <div className={classes.value} style={{ justifyContent: "flex-start" }}>
            <IconStatusPassed className={classes.iconStatus} />
            <span>
              {getValue(projectStatistics, 'passedTestResult')}
            </span>
            <IconStatusFailed className={classes.iconStatus} />
            <span>
              {getValue(projectStatistics, 'failedTestResult')}
            </span>
          </div>
        </div>
      </Grid>
    </Grid>
  );
}
